import React, {Component} from 'react';
import {withRouter} from "react-router-dom";
import {Layout, Menu, Icon, Dropdown} from 'antd';
import {NavLink} from 'react-router-dom';
import styles from '@/components/layout/AdminLayout.module.less';
import connect from "@/store/connect";
import {USER_TYPE_ADMIN} from '@/models/common/constMap'

const {SubMenu} = Menu;
const {Header, Sider, Content} = Layout;

@withRouter
@connect(({user}) => ({
  user,
}))
class AdminLayout extends Component {
  state = {
    collapsed: false,
  };

  componentDidMount() {
    const {user} = this.props;
    if (user.id === 0 || user.userType !== USER_TYPE_ADMIN) {
      this.props.history.push({pathname: "/login"});
    }
  }

  componentWillReceiveProps(nextProps) {
    const {user} = nextProps;
    if (user.id === 0 || user.userType !== USER_TYPE_ADMIN) {
      this.props.history.push({pathname: "/login"});
    }
  }

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  handleLogout = () => {
    const {dispatch} = this.props;
    dispatch({
      type: 'user/logout',
      payload: {},
    });
  };

  render() {
    const {user} = this.props;

    const AdminInfo = (
      <Menu>
        <Menu.Item key="0">
          <Icon type="user"/>
          <span>
            <NavLink to={{pathname: "/"}}>前台首页</NavLink>
          </span>
        </Menu.Item>
        <Menu.Divider/>
        <Menu.Item key="2" onClick={this.handleLogout}>
          <Icon type="logout"/>
          <span>退出登陆</span>
        </Menu.Item>
      </Menu>
    );

    return (
      <Layout>
        <Sider
          className={styles.siderHeightFool}
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <div className={styles.logo}>

            <span>
              <NavLink to={{pathname: "/admin/dashboard"}}>
                <img
                  src="data:image/svg+xml;base64,"
                  alt="logo"/>
                <span>管理后台</span>
              </NavLink>
            </span>
          </div>
          <Menu
            theme="dark"
            mode="inline"
            defaultSelectedKeys={['dashboard']}
            // defaultOpenKeys={['sub1']}
            style={{borderRight: 0}}
          >
            <Menu.Item key="dashboard">
              <NavLink to={{pathname: "/admin/dashboard"}}>
                <Icon type="pie-chart"/>
                <span>dashboard</span>
              </NavLink>
            </Menu.Item>
            <SubMenu
              key="sub1"
              title={<span><Icon type="book"/><span>文章管理</span></span>}
            >
              <Menu.Item key="1">
                <NavLink to={{pathname: "/admin/article/list"}}>
                  <span>文章列表</span>
                </NavLink>
              </Menu.Item>
              <Menu.Item key="2">
                <NavLink to={{pathname: "/admin/article/comments"}}>
                  <span>评论列表</span>
                </NavLink>
              </Menu.Item>
              <Menu.Item key="3">
                <NavLink to={{pathname: "/admin/article/tags"}}>
                  <span>标签列表</span>
                </NavLink>
              </Menu.Item>
            </SubMenu>
            <SubMenu
              key="sub2"
              title={<span><Icon type="user"/><span>用户管理</span></span>}
            >
              <Menu.Item key="4">
                <NavLink to={{pathname: "/admin/user/list"}}>
                  <span>用户列表</span>
                </NavLink>
              </Menu.Item>
            </SubMenu>
          </Menu>
        </Sider>
        <Layout>
          <Header style={{background: '#fff', padding: 0}}>
            <Icon
              className={styles.trigger}
              type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={this.toggle}
            />
            <Dropdown overlay={AdminInfo}>
              <span className={styles.dropDownLink}>
                <Icon type="smile" theme="twoTone" style={{marginRight: "10px"}}/>
                {user.username}
              </span>
            </Dropdown>
          </Header>
          <Content style={{
            margin: '2px 0', minHeight: 280,
          }}
          >
            {this.props.children}
          </Content>
        </Layout>
      </Layout>
    );
  }
}


export default AdminLayout;